<template>
  <div>
    <div class="drawContent">
      <!--  -->
      <el-tabs v-model="activeTab" class="voucher-tabs">
        <!--  -->
        <el-tab-pane name="password" :label="$t('voucher.password')">
          <div class="tab-content">
            <el-form ref="passwordForm" :model="form" :rules="rules">
              <el-form-item prop="password">
                <el-input v-model="form.password" type="text" :placeholder="$t('voucher.validPassword')" maxlength="6"
                  show-word-limit>
                </el-input>
              </el-form-item>
            </el-form>
          </div>
        </el-tab-pane>

        <!--  -->
        <el-tab-pane name="card" :label="$t('voucher.card')">
          <div class="tab-content">
            <el-form ref="cardForm" :model="form" :rules="rules">
              <el-form-item prop="card">
                <el-input v-model="form.card" :placeholder="$t('voucher.validCard')" maxlength="8"></el-input>
              </el-form-item>
            </el-form>
          </div>
        </el-tab-pane>

        <!--  -->
        <el-tab-pane name="face" :label="$t('voucher.face')">
          <div class="tab-content face-tab">
            <!--  -->
            <div class="register-type-selector">
              <el-radio-group v-model="faceType" @change="handleRegisterTypeChange">
                <el-radio-button :label="0">{{ $t('voucher.photoRegistration') }}</el-radio-button>
                <el-radio-button :label="1">{{ $t('voucher.featureValueRegistration') }}</el-radio-button>
              </el-radio-group>
            </div>

            <!--  -->
            <div v-if="faceType == 0" class="photo-register">
              <div class="upload-area">
                <el-upload class="avatar-uploader" action="#" :show-file-list="false" :on-change="handleUploadChange"
                  :auto-upload="false" accept="image/*">
                  <img v-if="faceImage" :src="faceImage" class="avatar" />
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
              </div>
              <!-- <div class="upload-tips">
                <p></p>
              </div> -->
            </div>

            <!--  -->
            <div v-if="faceType == 1" class="feature-register">
              <div class="feature-input-area">
                <el-input type="textarea" :rows="6" :placeholder="$t('common.placeholder')" v-model="faceFeature"
                  class="feature-textarea">
                </el-input>
                <!-- <div class="feature-tips">
                  <p>Base64</p>
                </div> -->
              </div>
            </div>

            <!--  -->
            <!-- <div class="action-buttons">
              <el-button type="primary" @click="handleRegister"></el-button>
              <el-button @click="handleReset"></el-button>
            </div> -->
          </div>
        </el-tab-pane>
        <!-- <el-tab-pane name="face" :label="$t('voucher.face')">
          <div class="tab-content face-tab">
            <div class="upload-area">
              <el-upload class="avatar-uploader" action="#" :show-file-list="false" :on-change="handleUploadChange"
                :auto-upload="false" accept="image/*">
                <img v-if="faceImage" :src="faceImage" class="avatar" />
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </div>
          </div>
        </el-tab-pane> -->
      </el-tabs>
    </div>

    <!--  -->
    <div class="dialog-footer">
      <el-button type="warning" @click="handleAddClose">
        {{ $t('common.cancel') }}
      </el-button>
      <el-button type="primary" @click="addConfirm">
        {{ $t('common.confirm') }}
      </el-button>
    </div>
  </div>
</template>

<script>
import { generateRandomString } from '@/utils/index.js'
export default {
  props: {
    formData: {
      type: Object,
      default: () => ({})
    },
    addOrEditType: {
      type: String,
      default: 'add'
    }
  },
  data () {
    return {
      visible: false,
      userId: '',
      name: '',
      form: {
        password: '',
        card: '',
        face: '',
        oldPassword: '',
        oldCard: '',
        oldFace: ''
      },
      keyId: {
        password: '',
        card: '',
        face: ''
      },
      rules: {
        password: [{ message: this.$t('voucher.validPassword'), trigger: ["blur"], pattern: /^\d{6}$/ }],
        card: [{ message: this.$t('voucher.validCard'), trigger: ["blur"], pattern: /^[A-Za-z0-9]{8}$/ },],
      },
      // 
      activeTab: 'password',
      faceType: 0, // 0-1-
      faceImage: '', //  base64  URL
      faceFeature: '', // 
      originalFaceData: null, // 
    };
  },
  created () {
    this.initialForm = JSON.parse(JSON.stringify(this.form));
    this.initialKeyId = JSON.parse(JSON.stringify(this.keyId));
    this.userId = this.formData.userId
    this.getVoucher()
  },
  methods: {
    // 
    async getVoucher () {
      let data = {
        page: 0,
        size: 100,
        userId: this.userId
      }
      try {
        const res = await this.$http.post('/getKey', { data })
        if (res.code == 200) {
          console.log(res)
          res.data.content.map(item => {
            if (item.type == 200) {
              this.form.card = item.code
              this.form.oldCard = item.code
              this.keyId.card = item.keyId
            } else if (item.type == 300) {
              // 
              let faceType = item.extra ? JSON.parse(item.extra).faceType : 0
              this.originalFaceData = {
                keyId: item.keyId,
                code: item.code,
                faceType
              }
              if (item.extra) {
                if (faceType == 0) {
                  this.faceImage = "data:image/jpeg;base64," + item.code
                } else if (faceType == 1) {
                  this.faceFeature = item.code
                }
                this.faceType = faceType
              }
              this.form.face = item.code
              this.form.oldFace = item.code
              this.keyId.face = item.keyId
            } else if (item.type == 400) {
              this.form.password = item.code
              this.form.oldPassword = item.code
              this.keyId.password = item.keyId
            }
          })
        } else {
          this.$message.error(res.message)
        }
      } catch (even) {
        console.log(even);
      }
    },

    // 
    handleRegisterTypeChange (value) {
      console.log(':', value);
      // 
      // this.faceImage = '';
      // this.faceFeature = '';
      // this.form.face = ''
    },

    handleUploadChange (file) {
      const reader = new FileReader();
      reader.onload = (e) => {
        console.log(e);
        let result = e.target.result
        this.faceImage = result
        this.form.face = result.split(',').pop()
      };
      reader.readAsDataURL(file.raw);
    },

    async addConfirm () {
      // 
      try {
        // 
        if (this.activeTab === 'face') {
          if (this.faceType === 1) {
            // 
            this.form.face = this.faceFeature
          }
          // handleUploadChangeform.face
        }
        await this.$refs.passwordForm.validate();
        await this.$refs.cardForm.validate();
        this.submitData();
      } catch (error) {
        this.$message.error(this.$t('config.msg_formFilled'));
      }
    },

    handleAddClose () {
      this.$emit('close-drawer', false);
    },

    async submitData () {
      if (!this.userId) {
        this.$message.error(this.$t('person.userNotExist'))
        return false
      }
      let keyAddList = []
      let keyEditList = []
      // 
      this.processPasswordVoucher(keyAddList, keyEditList)
      // 
      this.processCardVoucher(keyAddList, keyEditList)
      // 
      this.processFaceVoucher(keyAddList, keyEditList)
      if (keyAddList.length) {
        const res = await this.$http.post(
          "/insertKey",
          {
            data: keyAddList
          }
        );
        if (res.code == 200) {
          this.$message.success(this.$t('common.saveSuccess'))
          this.getVoucher()
        } else {
          if (res.data && res.data[0]) {
            this.$message.error(res.data[0].errmsg)
          }
        }
      }
      if (keyEditList.length) {
        const res = await this.$http.post(
          "/modifyKey",
          {
            data: keyEditList
          }
        );
        if (res.code == 200) {
          this.$message.success(this.$t('common.saveSuccess'))
          this.getVoucher()
        } else {
          if (res.data && res.data[0]) {
            this.$message.error(res.data[0].errmsg)
          }
        }
      }
      // 
      if (!keyAddList.length && !keyEditList.length) {
        this.$message.info(this.$t('common.noDataSaved'))
      }
    },

    // 
    processPasswordVoucher (keyAddList, keyEditList) {
      // 
      if (!this.form.password) return
      // 
      if (this.keyId.password) {
        if (this.form.password !== this.form.oldPassword) {
          keyEditList.push({
            keyId: this.keyId.password,
            userId: this.userId,
            type: '400',
            code: this.form.password
          })
        }
      } else {
        // 
        keyAddList.push({
          keyId: generateRandomString(),
          userId: this.userId,
          type: '400',
          code: this.form.password
        })
      }
    },

    // 
    processCardVoucher (keyAddList, keyEditList) {
      // 
      if (!this.form.card) return
      // 
      if (this.keyId.card) {
        if (this.form.card !== this.form.oldCard) {
          keyEditList.push({
            keyId: this.keyId.card,
            userId: this.userId,
            type: '200',
            code: this.form.card
          })
        }
      } else {
        // 
        keyAddList.push({
          keyId: generateRandomString(),
          userId: this.userId,
          type: '200',
          code: this.form.card
        })
      }
    },

    // 
    processFaceVoucher (keyAddList, keyEditList) {
      // 
      if (!this.form.face) return
      // 
      const hasFaceVoucher = this.originalFaceData !== null
      // 
      if (hasFaceVoucher) {
        // 
        const isDataChanged =
          this.form.face !== this.form.oldFace ||
          this.faceType !== this.originalFaceData.faceType

        if (isDataChanged) {
          keyEditList.push({
            keyId: this.keyId.face,
            userId: this.userId,
            type: '300',
            code: this.form.face,
            extra: {
              faceType: this.faceType
            }
          })
        }
      } else {
        // 
        keyAddList.push({
          keyId: generateRandomString(),
          userId: this.userId,
          type: '300',
          code: this.form.face,
          extra: {
            faceType: this.faceType
          }
        })
      }
    }

  },
};
</script>

