<template>
  <div>
    <el-container>
      <el-header class="topMain" style="height: 76px">
        <div class="mainL">
          <div class="manager">
            <img src="../../assets/head.png" alt="" />{{$t('aside.administrator')}}
          </div>
        </div>
        <div class="exit" @click="logout">
          <img src="../../assets/exit.png" alt="" />
          {{$t('aside.quit')}}
        </div>
      </el-header>
      <el-main>
        <div class="mainBox">
          <div class="mainTitle">{{$t('aside.msg_setTitle')}}</div>
          <div class="mainContent">
            <div>
              {{$t('aside.msg_setPsw')}}<input type="text" v-model="passwardForm.newPass" />
            </div>
            <div>
              {{$t('aside.msg_confirmPsw')}}<input type="text" v-model="passwardForm.confirmNewPass" />
            </div>
          </div>
          <div class="mainTip">{{$t('aside.msg_bottom')}}</div>
          <div class="mainBtn">
            <button class="confirmBtm" @click="confirm">{{$t('aside.msg_confirm')}}</button>
          </div>
        </div>
      </el-main>
    </el-container>
  </div>
</template>
<script>
export default {
  data () {
    return {
      passwardForm: {
        newPass: '',
        confirmNewPass: ''
      },
      language: 'en',
    };
  },
  created () {
    let lan = sessionStorage.getItem('language')
    if (lan) {
      this.language = lan
      this.$i18n.locale = this.language
    } else {
      this.language = 'Japanese'
      this.$i18n.locale = this.language
    }
  },
  methods: {
    changeLang () {
      this.$i18n.locale = this.language
      sessionStorage.setItem('language', this.language)
      this.languageUpdata()
    },
    async languageUpdata () {
      let that = this
      let languageList = {}
      languageList.pass = window.sessionStorage.getItem("pass");
      languageList.config = {}
      if (that.language == 'zh') {
        languageList.config.language = 0
      } else if (that.language == 'en') {
        languageList.config.language = 1
      } else if (that.language == 'Japanese') {
        languageList.config.language = 2
      } else if (that.language == 'es') {
        languageList.config.language = 3
      }

      console.log(languageList)
      const { data: res } = await this.$http.post(
        "/setConfig",
        languageList
      );
      console.log(res)
    },

    async confirm () {
      if (isNaN(this.passwardForm.newPass) == false && isNaN(this.passwardForm.confirmNewPass) == false && this.passwardForm.newPass == this.passwardForm.confirmNewPass && this.passwardForm.newPass != '' && this.passwardForm.confirmNewPass != '') {
        let data = {}
        // data.oldPass=window.sessionStorage.getItem("pass");
        data.oldPass = this.passwardForm.confirmNewPass
        data.newPass = this.passwardForm.newPass
        const { data: res } = await this.$http.post(
          "/setPassWord",
          data
        );
        console.log(res);
        if (res.result == 0) {
          sessionStorage.setItem('pass', res.data)
          this.$message({
            message: this.$t('aside.msg_conSuc'),
            type: "success",
          });
          this.$router.push({ path: "/home" });
        } else {
          this.$message.error(this.$t('aside.msg_conFail'));
        }
      } else if (this.passwardForm.confirmNewPass.indexOf(' ') >= 0) {
        this.$message.error(this.$t('aside.msg_nospace'));
      } else if (this.passwardForm.newPass != this.passwardForm.confirmNewPass) {
        this.$message.error(this.$t('aside.msg_reconfirm'));
      }
      else if (isNaN(this.passwardForm.newPass) == true && isNaN(this.passwardForm.confirmNewPass) == true) {
        this.$message.error(this.$t('aside.msg_number'));
      }
    },
    // 
    logout () {
      const that = this
      that.$confirm(this.$t('aside.tips_msg'), this.$t('aside.tips'), {
        type: "warning"
      }).then(async () => {
        that.toDoLogout()
      }).catch(() => {
        return false
      })
    },
    toDoLogout () {
      this.$router.push('/login')
      sessionStorage.removeItem("pass")
      sessionStorage.removeItem("userName")
      sessionStorage.removeItem("userPassword")
    }
  },
};
</script>
