              </el-card>
            </div>
          </el-tab-pane>

          <!--  -->
          <el-tab-pane :label="$t('config.clockConfiguration')" name="ntp">
            <div class="config-grid">
              <el-card class="config-card card-theme-display">
                <div class="config-card__header">
                  <i class="el-icon-connection config-card__header-icon"></i>
                  {{ $t('config.clockConfiguration') }}
                </div>
                <div class="config-card__body">
                  <el-form ref="ntpForm" :model="ntp" :rules="ntpRules" label-width="140px">
                    <div class="config-form-item">
                      <el-form-item :label="$t('config.timeSynchronizationSwitch')">
                        <el-switch v-model="ntp.ntp" :active-value="1" :inactive-value="0" active-color="#1890ff"
                          inactive-color="#ff4949">
                        </el-switch>
                      </el-form-item>
                    </div>
                    <div class="config-form-item">
                      <el-form-item :label="$t('config.timeSynchronizationServerIP')" prop="server">
                        <el-input v-model="ntp.server" :placeholder="$t('config.msg_please_enter')"></el-input>
                      </el-form-item>
                    </div>
                    <div class="config-form-item">
                      <el-form-item :label="$t('config.timedSynchronizationTime')" prop="hour">
                        <el-input v-model="ntp.hour" :placeholder="$t('config.msg_please_enter')"></el-input>
                      </el-form-item>
                    </div>
                    <div class="config-form-item">
                      <el-form-item :label="$t('config.timeZone')" prop="gmt">
                        <el-input v-model="ntp.gmt" :placeholder="$t('config.msg_please_enter')"></el-input>
                      </el-form-item>
