// src/api/request.js
import axios from 'axios'
import { Message } from 'element-ui'
import router from '../router'
import i18n from '../language/index'


// const baseURL = '/api'
const baseURL = (() => {
  const { protocol, hostname } = window.location;
  return `${protocol}//${hostname}:8080`; // 
})();

// axios
const service = axios.create({
  baseURL,
  timeout: 10000
})

// 
service.interceptors.request.use(
  config => {
    // token
    let token = sessionStorage.getItem("token")
    if (token) {
      config.headers['Authorization'] = token
    }
    return config
  },
  error => {
    return Promise.reject(error)
  }
)

// 
service.interceptors.response.use(
  response => {
    const res = response.data
    // 200
    if (res.code !== 200) {
      Message.error(res.message || i18n.t('error.requestFailed'))
      return Promise.reject(new Error(res.message || 'Error'))
    } else {
      return res
    }
  },
  error => {
    console.error(':', error)
    let errorMessage = i18n.t('error.networkError')
    if (error.code === 'ECONNABORTED' || error.message.includes('timeout')) {
      errorMessage = i18n.t('error.timeout')
      // 
      sessionStorage.removeItem('token')
      router.push('/login')
    } else if (error.response) {
      const status = error.response.status
      switch (status) {
        case 500:
          errorMessage = i18n.t('error.serverError')
          // 
          sessionStorage.removeItem('token')
          router.push('/login')
          break
        case 404:
          errorMessage = i18n.t('error.notFound')
          break
        case 401:
          errorMessage = i18n.t('error.unauthorized')
          // 
          sessionStorage.removeItem('token')
          router.push('/login')
          break
        default:
          errorMessage = i18n.t('error.unknownError') + `: ${status}`
      }

      if (error.response.data && error.response.data.message) {
        errorMessage = error.response.data.message
      }
    } else if (error.request) {
      errorMessage = i18n.t('error.noResponse')
    }
    Message.error(errorMessage)
    return Promise.reject(error)
  }
)

export default service